<template>
  <div>

    <Header></Header>

    <h3>Sign-up</h3>


    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>


    <div class="flex-container-form" v-if="showForm == true">
      <div class="flex-element-form">

        <form>

          <table>
            <tr>
              <td>Email: </td>
              <td>
                <input type="text" v-model="form.email">
              </td>
            </tr>
            <tr>
              <td>Username: </td>
              <td>
                <input type="text" v-model="form.username">
              </td>
            </tr>
            <tr>
              <td>Password: </td>
              <td>
                <input type="password" v-model="form.password">
              </td>
            </tr>
          </table>
        </form>

        <div style="text-align: center; margin-top:10px;">
          <button v-on:click="createUser()" class="btn btn-primary">Sign-up</button>
        </div>

        <div style="text-align: center; margin-top:10px; font-size: 0.9rem;">
          Already have an account? <router-link to="/sign-in">Sign-in</router-link>
        </div>

        <div v-if="loading">
          <div>Creating user...</div>
          <Spinner></Spinner>
        </div>
        

      </div>
    </div>


    <div class="flex-container" v-if="showForm == false">
      <div class="service-box" style="text-align: center">
        <div>User created successfully.</div>
      </div>
    </div>


  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
//import {getCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'SignUp',
  components: {
    Header, Spinner
  },
  methods: {

    createUser: function () {

      this.loading = true;
      this.errorMessage = null;

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'users';

      let that = this;

      const config = {
          headers: { 
            // "Authorization": "Bearer " + getCookie("sessionId")
          }
      };

      let data = {
        "username": this.form.username,
        "email": this.form.email,
        "password": this.form.password
      };

      //console.log("config="+JSON.stringify(config));

      axios
        .post(url, data, config)
        .then(function (response) {
          that.info = response;
          that.showForm = false;
          that.loading = false;
        })
        .catch(function (error) {
          that.loading = false;
          that.errorMessage = "Error: " + error.response.data.message;
        });
    },



  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      info: null,
      form: {
        username: "",
        email: "",
        password: ""
      },
      showForm: true
    }
  },
  mounted () {

  }
}
</script>


<style scoped>

table {
    border: 0px;
    border-spacing: 0px;
}

table td {
    border: 0px;
    padding: 1px;
    background-color: transparent;
}

</style>