<template>
  <div>
    <Header></Header>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <div v-if="loading"><Spinner></Spinner></div>

    <div class="flex-container-games">
      <div v-for="game in games" v-bind:key="game.id" class="flex-element-games">
        <div class="game-title"><router-link :to="'/play/' + game.templateVersion + '/' + game.id">{{ game.name }}</router-link></div>
        <div class="game-description">{{ game.description }}</div>
        <div class="game-details">Created: {{ game.createdDate }}</div>
        <div class="game-details">Updated: {{ game.updatedDate }}</div>
        <div class="game-details">Published: {{ game.published }}</div>
        <div class="game-details">Engine version: {{ game.templateVersion }}</div>
        <router-link :to="'/play/' + game.templateVersion + '/' + game.id" tag="button" class="btn btn-primary">Play</router-link>
        <span style="margin-left:5px;"></span>
        <router-link :to="'/create/' + game.templateVersion + '?gameId=' + game.id" tag="button" class="btn btn-primary">Edit</router-link>
      </div>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {getCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'MyGames',
  components: {
    Header, Spinner
  },
  methods: {

    listMyGames: function () {
      //this.services = this.info.data.Items;
      this.loading = true;
      this.errorMessage = null;
      this.info = null;
      //this.services = [];

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'myGames';

      let that = this;

      const config = {
          //headers: { 
          //  "Authorization": "Bearer " + getCookie("sessionId")
          //},
          withCredentials: true
      };

      axios
        .get(url, config)
        .then(function (response) {
          that.info = response;
          that.games = response.data.Items;
          that.loading = false;
        })
        .catch(function (error) {
          that.loading = false;
          if (error.response && error.response.status==401) {
            that.$router.push({ path: 'sign-in?message=session-expired' });
          } else {
            that.errorMessage = "Error: " + error;
          }
        });
    },

  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      info: null,
      games: [],
      sessionId: null
    }
  },
  mounted () {

    this.sessionId = getCookie("sessionId");
    //console.log("sessionId == " + this.sessionId);
    this.listMyGames();
   

  }
}
</script>


