<template>
  <div>
    <Header></Header>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <p class="home">We will publish here the best games created by our users.</p>

    <div v-if="loading"><Spinner></Spinner></div>

    <div class="flex-container-games">
      <div v-for="game in games" v-bind:key="game.id" class="flex-element-games">
        <div class="game-title"><router-link :to="'/play/' + game.templateVersion + '/' + game.id">{{ game.name }}</router-link></div>
        <div class="game-description">{{ game.description }}</div>
      </div>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {getCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'Game',
  components: {
    Header, Spinner
  },
  methods: {

    updateGames: function () {
      //this.services = this.info.data.Items;
      this.loading = true;
      this.errorMessage = null;
      this.info = null;
      this.services = [];

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'games';

      let that = this;

      const config = {
          headers: { 
            //"Authorization": "Bearer " + getCookie("sessionId")
          }
      };

      axios
        .get(url, config)
        .then(function (response) {
          that.info = response;
          that.games = response.data.Items;
          that.loading = false;
        })
        .catch(function (error) {
          that.loading = false;
          that.errorMessage = "Error: " + error;
        });
    },

  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      info: null,
      games: [],
      sessionId: null
    }
  },
  mounted () {

    this.sessionId = getCookie("sessionId");
    //console.log("sessionId == " + this.sessionId);
    this.updateGames();
   

  }
}
</script>


<style scoped>

.home {
  max-width: 900px;
  margin: auto;
  font-size: 1.2em;
}

</style>