<template>
  <div>

    <Header ref="header"></Header>

    <h3>Sign-in</h3>


    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>


    <div class="flex-container-form" v-if="showForm == true">
      <div class="flex-element-form">

        <form>

          <table>
            <tr>
              <td>Username: </td>
              <td>
                <input type="text" v-model="form.username">
              </td>
            </tr>
            <tr>
              <td>Password: </td>
              <td>
                <input type="password" v-model="form.password">
              </td>
            </tr>
          </table>
        </form>

        <div style="text-align: center; margin-top:10px;">
          <button v-on:click="signIn()" class="btn btn-primary">Sign-in</button>
        </div>

        <div style="text-align: center; margin-top:10px; font-size: 0.9rem;">
          Don't have an account? <router-link to="/sign-up">Sign-up</router-link>
        </div>

        <div v-if="loading">
          <Spinner></Spinner>
        </div>
        

      </div>
    </div>


    <div class="flex-container" v-if="showForm == false">
      <div class="service-box" style="text-align: center">
        <div>Welcome back.</div>
      </div>
    </div>


  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {setCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'SignIn',
  components: {
    Header, Spinner
  },
  methods: {

    signIn: function () {

      this.loading = true;
      this.errorMessage = null;

      //let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      //let url = apiBaseUrl + 'userSessions';
      //url = "https://dev.easygamemaker.com/login"; //TODO: arrumar
      let url = process.env.VUE_APP_LOGIN_URL;

      let that = this;

      const config = {
          headers: { 
            // "Authorization": "Bearer " + getCookie("sessionId")
          }
      };

      let data = {
        "username": this.form.username,
        "password": this.form.password
      };

      //console.log("config="+JSON.stringify(config));

      axios
        .post(url, data, config)
        .then(function (response) {
          that.info = response;
          that.showForm = false;
          that.loading = false;
          //that.sessionId = response.data.userSessionId;
          setCookie("authenticated", "true");
          that.$refs.header.update();
        })
        .catch(function (error) {
          that.loading = false;
          //that.errorMessage = "Error: " + error;
          that.errorMessage = error.response.data.message;
        });
    },



  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      info: null,
      //sessionId: null,
      form: {
        username: "",
        password: ""
      },
      showForm: true
    }
  },
  mounted () {
    if (this.$route.query.message == "session-expired") {
      this.errorMessage = "Session expired, please login again.";
    }
  }
}
</script>


<style scoped>

table {
    border: 0px;
    border-spacing: 0px;
}

table td {
    border: 0px;
    padding: 1px;
    background-color: transparent;
}

</style>