<template>
  <div>
    <Header ref="header"></Header>

    <h3>Logout</h3>

    <div v-if="message != null">
      <br><div class="alert alert-primary" role="alert" style="display:inline-block;">{{ message }}</div>
    </div>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <div v-if="loading"><Spinner></Spinner></div>

  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {deleteCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'Logout',
  components: {
    Header, Spinner
  },
  methods: {

    logout: function () {
      this.loading = true;
      this.errorMessage = null;

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'logout';

      let that = this;

      const config = {
          //headers: { 
          //  "Authorization": "Bearer " + getCookie("sessionId")
          //},
          withCredentials: true
      };

      const data = null;

      axios
        .post(url, data, config)
        .then(function (response) {
          deleteCookie("authenticated");
          that.$refs.header.update();
          that.message = response.data.message;
          that.loading = false;
        })
        .catch(function (error) {
          deleteCookie("authenticated");
          that.$refs.header.update();
          that.loading = false;
          if (error.response && error.response.status==401) {
            that.$router.push({ path: 'sign-in?message=session-expired' });
          } else {
            that.errorMessage = "Error: " + error;
          }
        });
    },

  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      message: null
    }
  },
  mounted () {

    this.logout();

  }
}
</script>


