<template>
  <div>
    <Header></Header>

    <h3>My Profile</h3>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <div v-if="loading"><Spinner></Spinner></div>

    <div class="flex-container">
      <div class="service-box">
        <div class="service-box-description">Username: {{ user.username.S }}</div>
        <div class="service-box-description">Email: {{ user.email.S }}</div>
        <div class="service-box-description">Member since: {{ user.createdDate.S }}</div>
        <div class="service-box-description"><router-link to="/my-games">My Games</router-link></div>
      </div>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {getCookie} from '@/lib/mmgUtil.js';

export default {
  name: 'MyProfile',
  components: {
    Header, Spinner
  },
  methods: {

    loadUserProfile: function () {
      //this.services = this.info.data.Items;
      this.loading = true;
      this.errorMessage = null;
      this.info = null;
      //this.services = [];

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'myProfile';

      let that = this;

      const config = {
          //headers: { 
          //  "Authorization": "Bearer " + getCookie("sessionId")
          //},
          withCredentials: true
      };

      axios
        .get(url, config)
        .then(function (response) {
          that.info = response;
          that.user = response.data.Item;
          that.loading = false;
        })
        .catch(function (error) {
          that.loading = false;
          if (error.response && error.response.status==401) {
            that.$router.push({ path: 'sign-in?message=session-expired' });
          } else {
            that.errorMessage = "Error: " + error;
          }
        });
    },

  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      info: null,
      user: {
        username: "",
        createdDate: "",
        email: ""
      },
      sessionId: null
    }
  },
  mounted () {

    this.sessionId = getCookie("sessionId");
    //console.log("sessionId == " + this.sessionId);
    this.loadUserProfile();
   

  }
}
</script>


<style scoped>

.flex-container {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  /*align-items: center;*/
  align-content: center;
  text-align: left;
  justify-content: center;
}

.service-box {
    margin-right: 3px;
    margin-bottom: 3px;
    padding: 10px;
    /*width: 220px;*/
    /*height: 240px;*/
    overflow: hidden;
    background-color: #eeeeee;
}

.service-box-title {
    font-weight: bold;
}
.service-box-description {
    color: #666666;
    font-size: 0.9em;
}


</style>