<template>
  <div>

    <Header></Header>

    <h3>Contact Us</h3>

    <div class="home">
      <p>Your feedback is very important to us.</p>
    </div>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>


    <div class="flex-container-form" v-if="showForm == true">
      <div class="flex-element-form">

        <form>

          <table>
            <tr>
              <td style="text-align: right">Contact type: </td>
              <td>
                <select v-model="form.type">
                  <option v-for="type in types" v-bind:value="type.value" v-bind:key="type.value">
                  {{ type.text }}
                  </option>
                </select>
              </td>
            </tr>
            <tr>
              <td style="text-align: right">Subject: </td>
              <td>
                <input class="textfield" type="text" v-model="form.subject">
              </td>
            </tr>
            <tr>
              <td style="text-align: right">Message: </td>
              <td>
                <textarea class="text-area" v-model="form.message"></textarea>
              </td>
            </tr>
            <tr>
              <td style="text-align: right">E-mail for response (optional): </td>
              <td>
                <input class="textfield" type="text" v-model="form.email">
              </td>
            </tr>
          </table>
        </form>

        <div style="text-align: center; margin-top:10px;">
          <button v-on:click="submit()" class="btn btn-primary">Submit</button>
        </div>

        <div v-if="loading">
          <Spinner></Spinner>
        </div>
        

      </div>
    </div>


    <div class="flex-container" v-if="showForm == false">
      <div class="service-box" style="text-align: center">
        <div>Your message was sent. Thank you for contacting us.</div>
      </div>
    </div>


  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';

export default {
  name: 'ContactUs',
  components: {
    Header, Spinner
  },
  methods: {

    submit: function () {

      this.loading = true;
      this.errorMessage = null;

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'contacts';

      let that = this;

      const config = {
          withCredentials: true
      };

      let data = {
        "type": this.form.type,
        "email": this.form.email,
        "message": this.form.message,
        "subject": this.form.subject
      };

      axios
        .post(url, data, config)
        .then(function (response) { // eslint-disable-line no-unused-vars
          //that.info = response;
          that.showForm = false;
          that.loading = false;
        })
        .catch(function (error) {
          that.loading = false;
          //that.errorMessage = "Error: " + error;
          that.errorMessage = error.response.data.message;
        });
    },



  },
  data() {
    return {
      loading: false,
      errorMessage: null,
      //info: null,
      //sessionId: null,
      form: {
        type: "",
        email: "",
        message: "",
        subject: ""
      },
      types: [
        { value: 'SUGGESTION', text: 'Suggestion / feature request' },
        { value: 'BUG_REPORT', text: 'Bug report' },
        { value: 'FEEDBACK', text: 'Feedback' },
        { value: 'SUPPORT', text: 'Support / help' },
        { value: 'OTHER', text: 'Other' }
      ],
      showForm: true
    }
  },
  mounted () {

  }
}

</script>


<style scoped>

.home {
  max-width: 900px;
  margin: auto;
  font-size: 1.2em;
}

.text-area {
  width: 300px;
  height: 100px;
}

.textfield {
  width: 300px;
}

table {
    border: 0px;
    border-spacing: 0px;
}

table td {
    border: 0px;
    padding: 1px;
    background-color: transparent;
}

</style>